import React from 'react'
import Posts from './Posts.js'
import './Article.css' 
import ReactDOM from 'react-dom'
import Post from './Post.jsx'
import $ from 'jquery'
import SideBar from './SideBar';


export default function Article(){

	function posts(){
       
		return Posts.map((image) =>{
      
        var pId = '#'+image.title.replace(/\s/g, "-").replace('$','').replace('.','').replace('()','').toLowerCase();
        var tmbImg = require('./'+image.title.replace(/\s/g, "-").replace('$','').replace('.','').replace('()','').toLowerCase()+'.jpg').default

			return (
               
                 <div onClick={function(e){fullPost();$(pId).fadeIn(400);}}>

                 <a href={pId}>
                  <div className="thumb" style={{backgroundImage:`url(${tmbImg})`}}></div>
                  <h2 key={image.title}>{image.title}</h2>
                  <em key={image.data}>{image.data}</em>
                  <p key={image.content}>{ image.content }</p>
                  </a>
                 </div>
				)
		})
	}

	return(
       <div className="article-prev">
      
        {posts()}
        
        </div>
     
		)
}

export function fullPost(){
	var blogue = document.createElement('DIV');
    blogue.setAttribute('id','fullBlog');
    document.getElementById('app').append(blogue);
    ReactDOM.render(<div ><Post/><SideBar/></div>,document.getElementById('fullBlog'));
}

