import React, {Component} from 'react';
import ScriptTag from 'react-script-tag';


class Twitter extends Component {

	render() {
	return(
		<div className="twitter">
		<a className="twitter-timeline" data-width="300" href="https://twitter.com/infjape?ref_src=twsrc%5Etfw">@infjape</a> <ScriptTag async type="text/javascript" src="https://platform.twitter.com/widgets.js" />
		</div>
		)
}

}

export default Twitter;